html {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
body {
  background-color: black;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.full-display {
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 80%;
  height: 300px;
  position: relative;
  top: 0;
  transform: translateX(5%);
}

.display {
  position: relative;
  transform: scale(0.5);
  left: 0px;
  top: -100px;
}

.segment {
  height: 125px;
  width: 15px;
  background-color: #990000;
  border-radius: 9999px;
  box-shadow: 0px 0px 25px #990000;
}

.A {
  position: absolute;
  top: -45px;
  left: 90px;
  transform: rotate(90deg);
}
.B {
  position: absolute;
  top: 20px;
  left: 160px;
}
.C {
  position: absolute;
  top: 150px;
  left: 160px;
}
.D {
  position: absolute;
  top: 215px;
  left: 90px;
  transform: rotate(90deg);
}
.E {
  position: absolute;
  top: 150px;
  left: 20px;
}
.F {
  position: absolute;
  top: 20px;
  left: 20px;
}
.G {
  position: absolute;
  top: 85px;
  left: 90px;
  transform: rotate(90deg);
}

/* separador */
.dot {
  transform: translateX(450%);
  position: absolute;
  height: 20px;
  width: 20px;
  background-color: #990000;
  border-radius: 9999px;
  box-shadow: 0px 0px 25px #990000;
}
.dot1 {
  top: 62px;
}
.dot2 {
  top: 187px;
}

@media (max-width: 1000px) {
  .display {
    transform: scale(0.4);
  }
}
@media (max-width: 900px) {
  .display {
    transform: scale(0.3);
  }
}
@media (max-width: 700px) {
  .display {
    transform: scale(0.2);
  }
}
@media (max-width: 500px) {
  .display {
    transform: scale(0.15);
  }
}
@media (max-width: 350px) {
  .display {
    transform: scale(0.1);
  }
}
